import React from 'react';
import { 
  Search, Package, Heart, ShoppingCart, Filter, 
  FolderOpen, AlertCircle, RefreshCw, ArrowRight,
  Sparkles, Frown
} from 'lucide-react';

/**
 * Modern Empty State Components
 * Beautiful, helpful empty states with actions
 */

// Base Empty State
export const EmptyState = ({ 
  icon: Icon = Package,
  title,
  description,
  action,
  secondaryAction,
  size = 'md',
  className = ''
}) => {
  const sizes = {
    sm: {
      container: 'py-8 px-4',
      icon: 'w-12 h-12',
      iconWrapper: 'w-16 h-16',
      title: 'text-base',
      description: 'text-sm',
      button: 'px-4 py-2 text-sm'
    },
    md: {
      container: 'py-12 px-6',
      icon: 'w-16 h-16',
      iconWrapper: 'w-24 h-24',
      title: 'text-lg',
      description: 'text-base',
      button: 'px-5 py-2.5 text-sm'
    },
    lg: {
      container: 'py-16 px-8',
      icon: 'w-20 h-20',
      iconWrapper: 'w-32 h-32',
      title: 'text-xl',
      description: 'text-lg',
      button: 'px-6 py-3 text-base'
    }
  };

  const s = sizes[size];

  return (
    <div className={`flex flex-col items-center justify-center text-center ${s.container} ${className}`}>
      {/* Animated Icon Container */}
      <div className={`
        ${s.iconWrapper} rounded-full 
        bg-gradient-to-br from-gray-100 to-gray-50 
        flex items-center justify-center mb-6
        relative overflow-hidden
      `}>
        {/* Subtle animation ring */}
        <div className="absolute inset-0 rounded-full border-2 border-dashed border-gray-200 animate-spin-slow" />
        <Icon className={`${s.icon} text-gray-400`} />
      </div>

      {/* Title */}
      {title && (
        <h3 className={`${s.title} font-bold text-gray-900 mb-2`}>
          {title}
        </h3>
      )}

      {/* Description */}
      {description && (
        <p className={`${s.description} text-gray-500 max-w-md mb-6`}>
          {description}
        </p>
      )}

      {/* Actions */}
      {(action || secondaryAction) && (
        <div className="flex flex-col sm:flex-row gap-3">
          {action && (
            <button
              onClick={action.onClick}
              className={`
                ${s.button} min-h-[44px]
                bg-primary-500 text-white font-semibold rounded-xl
                hover:bg-primary-600 transition-colors
                flex items-center justify-center gap-2
              `}
            >
              {action.icon && <action.icon className="w-4 h-4" />}
              {action.label}
            </button>
          )}
          {secondaryAction && (
            <button
              onClick={secondaryAction.onClick}
              className={`
                ${s.button} min-h-[44px]
                bg-gray-100 text-gray-700 font-semibold rounded-xl
                hover:bg-gray-200 transition-colors
                flex items-center justify-center gap-2
              `}
            >
              {secondaryAction.icon && <secondaryAction.icon className="w-4 h-4" />}
              {secondaryAction.label}
            </button>
          )}
        </div>
      )}

      <style>{`
        @keyframes spin-slow {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
        .animate-spin-slow {
          animation: spin-slow 20s linear infinite;
        }
      `}</style>
    </div>
  );
};

// No Search Results
export const NoSearchResults = ({ query, onClear, onSuggest }) => (
  <EmptyState
    icon={Search}
    title={`Nincs tallat: "${query}"`}
    description="Prblj ms kulcsszavakat hasznlni, vagy bngssz a kategrik kztt."
    action={onClear && {
      label: 'Keress trlse',
      icon: RefreshCw,
      onClick: onClear
    }}
    secondaryAction={onSuggest && {
      label: 'AI javaslatok',
      icon: Sparkles,
      onClick: onSuggest
    }}
  />
);

// No Products in Category
export const NoCategoryProducts = ({ category, onShowAll }) => (
  <EmptyState
    icon={FolderOpen}
    title={`Nincs termk: ${category}`}
    description="Ez a kategria jelenleg res. Nzd meg a tbbi kategrit!"
    action={onShowAll && {
      label: 'sszes termk',
      icon: ArrowRight,
      onClick: onShowAll
    }}
  />
);

// Empty Wishlist
export const EmptyWishlist = ({ onBrowse }) => (
  <EmptyState
    icon={Heart}
    title="res a kvnsglistd"
    description="Mg nem adtl hozz termkeket. Bngssz s jelld meg kedvenceidet!"
    action={onBrowse && {
      label: 'Bngszs',
      icon: ArrowRight,
      onClick: onBrowse
    }}
  />
);

// Empty Cart
export const EmptyCart = ({ onBrowse }) => (
  <EmptyState
    icon={ShoppingCart}
    title="res a kosarad"
    description="Mg nem tettl semmit a kosrba. Fedezd fel knlatunkat!"
    action={onBrowse && {
      label: 'Vsrls',
      icon: ArrowRight,
      onClick: onBrowse
    }}
  />
);

// No Filter Results
export const NoFilterResults = ({ onClearFilters }) => (
  <EmptyState
    icon={Filter}
    title="Nincs tallat a szrsre"
    description="A megadott szrknek egyetlen termk sem felel meg. Prbld laztani a feltteleket."
    action={onClearFilters && {
      label: 'Szrk trlse',
      icon: RefreshCw,
      onClick: onClearFilters
    }}
  />
);

// Error State
export const ErrorState = ({ message, onRetry }) => (
  <EmptyState
    icon={AlertCircle}
    title="Hiba trtnt"
    description={message || "Valami nem sikerlt. Krlek prbld jra!"}
    action={onRetry && {
      label: 'jraprbls',
      icon: RefreshCw,
      onClick: onRetry
    }}
  />
);

// Coming Soon
export const ComingSoon = ({ feature }) => (
  <EmptyState
    icon={Sparkles}
    title="Hamarosan"
    description={feature ? `${feature} - dolgozunk rajta!` : "Ez a funkci hamarosan elrhet!"}
  />
);

// Not Found (404)
export const NotFound = ({ onGoHome }) => (
  <EmptyState
    icon={Frown}
    title="Az oldal nem tallhat"
    description="A keresett oldal nem ltezik vagy thelyezsre kerlt."
    action={onGoHome && {
      label: 'Vissza a foldalra',
      icon: ArrowRight,
      onClick: onGoHome
    }}
    size="lg"
  />
);

export default {
  EmptyState,
  NoSearchResults,
  NoCategoryProducts,
  EmptyWishlist,
  EmptyCart,
  NoFilterResults,
  ErrorState,
  ComingSoon,
  NotFound
};
