/**
 * Sidebar Filters
 */
const SidebarFilters = ({ 
  products, 
  filters, 
  onFilterChange, 
  priceRange, 
  onPriceRangeChange,
  isOpen,
  onClose
}) => {
  // Extract unique values from products
  const filterOptions = useMemo(() => {
    const brands = new Map();
    const colors = new Map();
    
    products.forEach(p => {
      if (p.brand) brands.set(p.brand, (brands.get(p.brand) || 0) + 1);
      if (p.color) colors.set(p.color, (colors.get(p.color) || 0) + 1);
    });
    
    const prices = products.map(p => p.salePrice || p.price).filter(p => p > 0);
    const minPrice = Math.min(...prices) || 0;
    const maxPrice = Math.max(...prices) || 1000000;
    
    return {
      brands: Array.from(brands.entries()).sort((a, b) => b[1] - a[1]),
      colors: Array.from(colors.entries()).sort((a, b) => b[1] - a[1]),
      priceMin: minPrice,
      priceMax: maxPrice
    };
  }, [products]);

  const previewCounts = useMemo(() => {
    const brandCounts = new Map();
    const colorCounts = new Map();
    let inStock = 0;

    products.forEach((p) => {
      const price = p.salePrice || p.price || 0;
      if (price < priceRange[0] || price > priceRange[1]) return;

      const isInStock = p.inStock ?? p.in_stock ?? true;
      const brandOk = !filters.brands?.length || filters.brands.includes(p.brand);
      const colorOk = !filters.colors?.length || filters.colors.includes(p.color);

      if (brandOk && colorOk && isInStock) inStock += 1;

      const baseForBrand = (filters.inStockOnly ? isInStock : true) && colorOk;
      if (baseForBrand && p.brand) {
        brandCounts.set(p.brand, (brandCounts.get(p.brand) || 0) + 1);
      }

      const baseForColor = (filters.inStockOnly ? isInStock : true) && brandOk;
      if (baseForColor && p.color) {
        colorCounts.set(p.color, (colorCounts.get(p.color) || 0) + 1);
      }
    });

    return { brands: brandCounts, colors: colorCounts, inStock };
  }, [products, filters, priceRange]);

  const toggleFilter = (type, value) => {
    const current = filters[type] || [];
    const updated = current.includes(value)
      ? current.filter(v => v !== value)
      : [...current, value];
    onFilterChange({ ...filters, [type]: updated });
  };

  const FilterSection = ({ title, items, type, icon: Icon }) => (
    <div className="border-b border-gray-100 pb-4 mb-4">
      <h4 className="font-semibold text-gray-900 mb-3 flex items-center gap-2">
        {Icon && <Icon className="w-4 h-4 text-gray-400" />}
        {title}
      </h4>
      <div className="space-y-2 max-h-40 overflow-y-auto">
        {items.map(([item, count]) => {
          const preview = type === 'brands'
            ? previewCounts.brands.get(item)
            : type === 'colors'
              ? previewCounts.colors.get(item)
              : count;
          return (
          <label key={item} className="flex items-center justify-between gap-2 cursor-pointer group">
            <div className="flex items-center gap-2">
            <input
              type="checkbox"
              checked={(filters[type] || []).includes(item)}
              onChange={() => toggleFilter(type, item)}
              className="w-4 h-4 rounded border-gray-300 text-primary-500 focus:ring-primary-500"
            />
            <span className="text-sm text-gray-600 group-hover:text-gray-900">{item}</span>
            </div>
            <span className="text-xs text-gray-400">{preview ?? count}</span>
          </label>
        )})}
      </div>
    </div>
  );

  const content = (
    <div className="p-4 sm:p-0">
      <div className="flex items-center justify-between mb-6 sm:hidden">
        <h3 className="text-lg font-bold text-gray-900">Szrk</h3>
        <button onClick={onClose} className="p-2 hover:bg-gray-100 rounded-lg">
          <X className="w-5 h-5" />
        </button>
      </div>

      {/* Price Range */}
      <div className="border-b border-gray-100 pb-4 mb-4">
        <h4 className="font-semibold text-gray-900 mb-3 flex items-center gap-2">
          <Tag className="w-4 h-4 text-gray-400" />
          rtartomny
        </h4>
        <div className="space-y-3">
          <input
            type="range"
            min={filterOptions.priceMin}
            max={filterOptions.priceMax}
            value={priceRange[1]}
            onChange={(e) => onPriceRangeChange([priceRange[0], parseInt(e.target.value)])}
            className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer accent-primary-500"
          />
          <div className="flex justify-between text-sm text-gray-500">
            <span>{priceRange[0].toLocaleString('hu-HU')} Ft</span>
            <span>{priceRange[1].toLocaleString('hu-HU')} Ft</span>
          </div>
        </div>
      </div>

      {filterOptions.brands.length > 0 && (
        <FilterSection title="Mrka" items={filterOptions.brands} type="brands" icon={Package} />
      )}
      
      {filterOptions.colors.length > 0 && (
        <FilterSection title="Szn" items={filterOptions.colors} type="colors" icon={Tag} />
      )}

      {/* Stock Filter */}
      <div className="pb-4">
        <label className="flex items-center gap-2 cursor-pointer">
          <input
            type="checkbox"
            checked={filters.inStockOnly}
            onChange={() => onFilterChange({ ...filters, inStockOnly: !filters.inStockOnly })}
            className="w-4 h-4 rounded border-gray-300 text-primary-500 focus:ring-primary-500"
          />
          <span className="text-sm text-gray-700">Csak kszleten ({previewCounts.inStock})</span>
        </label>
      </div>

      {/* Clear Filters */}
      <button
        onClick={() => onFilterChange({})}
        className="w-full py-2 text-sm text-primary-500 hover:text-primary-600 font-medium"
      >
        Szrk trlse
      </button>
    </div>
  );

  return (
    <>
      {/* Desktop Sidebar */}
      <div className="hidden lg:block w-64 shrink-0">
        <div className="sticky top-24 bg-white rounded-2xl border border-gray-100 p-5">
          <h3 className="font-bold text-gray-900 mb-4 flex items-center gap-2">
            <SlidersHorizontal className="w-5 h-5" />
            Szrk
          </h3>
          {content}
        </div>
      </div>

      {/* Mobile Drawer */}
      {isOpen && (
        <div className="lg:hidden fixed inset-0 z-50">
          <div className="absolute inset-0 bg-black/50" onClick={onClose} />
          <div className="absolute right-0 top-0 bottom-0 w-80 max-w-full bg-white animate-fade-in-right">
            {content}
          </div>
        </div>
      )}
    </>
  );
};

/**
 * Related Categories
 */
const RelatedCategories = ({ currentCategory, allCategories, onCategoryChange }) => {
  // Find related categories (same "type" or popular ones)
  const related = useMemo(() => {
    return allCategories
      .filter(cat => cat.name !== currentCategory && cat.name !== 'sszes' && cat.count > 0)
      .slice(0, 6);
  }, [allCategories, currentCategory]);

  if (related.length === 0) return null;

  return (
    <div className="bg-gray-50 rounded-2xl p-4 sm:p-6 mb-6">
      <h3 className="font-bold text-gray-900 mb-4 flex items-center gap-2">
        <Grid3X3 className="w-5 h-5 text-gray-400" />
        Kapcsold kategrik
      </h3>
      <div className="flex flex-wrap gap-2">
        {related.map(cat => {
          const Icon = getCategoryIcon(cat.name);
          return (
            <button
              key={cat.name}
              onClick={() => onCategoryChange(cat.name)}
              className="flex items-center gap-2 px-3 py-2 bg-white rounded-xl border border-gray-200 hover:border-primary-300 hover:bg-primary-50 transition-colors tap-scale"
            >
              <Icon className="w-4 h-4 text-gray-500" />
              <span className="text-sm font-medium text-gray-700">{cat.name}</span>
              <span className="text-xs text-gray-400">({cat.count})</span>
            </button>
          );
        })}
      </div>
    </div>
  );
};

