/**
 * Price Statistics Card
 */
const PriceStats = ({ products, stats }) => {
  const computedStats = useMemo(() => {
    if (stats && typeof stats.total === 'number') {
      return {
        min: stats.minPrice || 0,
        max: stats.maxPrice || 0,
        avg: stats.avgPrice || 0,
        inStock: stats.inStockCount || 0
      };
    }
    if (!products || products.length === 0) return null;
    const prices = products.map(p => p.salePrice || p.price).filter(p => p > 0);
    if (prices.length === 0) return null;
    return {
      min: Math.min(...prices),
      max: Math.max(...prices),
      avg: prices.reduce((a, b) => a + b, 0) / prices.length,
      inStock: products.filter(p => p.inStock !== false).length
    };
  }, [products, stats]);
  
  if (!computedStats) return null;
  
  return (
    <div className="grid grid-cols-2 sm:grid-cols-4 gap-3 mb-6">
      {[
        { label: 'Legolcsbb', value: formatPrice(computedStats.min), color: 'text-green-600' },
        { label: 'tlagr', value: formatPrice(computedStats.avg), color: 'text-gray-900' },
        { label: 'Legdrgbb', value: formatPrice(computedStats.max), color: 'text-gray-900' },
        { label: 'Kszleten', value: `${computedStats.inStock} db`, color: 'text-primary-500' }
      ].map((stat, i) => (
        <div key={i} className="bg-white rounded-xl p-3 sm:p-4 border border-gray-100">
          <p className="text-xs text-gray-500 mb-1">{stat.label}</p>
          <p className={`text-base sm:text-lg font-bold ${stat.color}`}>{stat.value}</p>
        </div>
      ))}
    </div>
  );
};

/**
 * Featured Products
 */
const FeaturedProducts = ({ products, onProductClick, onWishlistToggle, wishlist, onAddToCart }) => {
  const featured = useMemo(() => {
    return products
      .filter(p => p.salePrice && p.price > p.salePrice)
      .sort((a, b) => ((b.price - b.salePrice) / b.price) - ((a.price - a.salePrice) / a.price))
      .slice(0, 6);
  }, [products]);
  
  if (featured.length === 0) return null;
  
  return (
    <div className="mb-6">
      <h3 className="font-bold text-gray-900 mb-4 flex items-center gap-2">
        <Star className="w-5 h-5 text-amber-500" />
        Kiemelt ajnlatok
      </h3>
      <div className="flex gap-4 overflow-x-auto pb-4 scrollbar-hide -mx-4 px-4">
        {featured.map((product, index) => (
          <div key={product.id} className="w-44 sm:w-52 shrink-0">
            <EnhancedProductCard
              product={product}
              onQuickView={onProductClick}
              onToggleWishlist={onWishlistToggle}
              onAddToCart={onAddToCart}
              isWishlisted={wishlist?.includes(product.id)}
              index={index}
            />
          </div>
        ))}
      </div>
    </div>
  );
};

/**
 * Compact Product Card - mint EnhancedProductCard: like/dislike, r formzs
 */
const CompactProductCard = ({ product, onQuickView, onToggleWishlist, isWishlisted, onAddToCart, onCompare, isComparing }) => {
  const [feedbackState, setFeedbackState] = useState(() => ({
    liked: isProductLiked(product?.id),
    disliked: isProductDisliked(product?.id)
  }));
  useEffect(() => {
    if (product?.id) setFeedbackState({ liked: isProductLiked(product.id), disliked: isProductDisliked(product.id) });
  }, [product?.id]);
  const displayPrice = product.salePrice || product.price;
  const hasDiscount = product.salePrice && product.price > product.salePrice;
  const discountPct = hasDiscount ? Math.round(((product.price - product.salePrice) / product.price) * 100) : 0;

  return (
    <div className="flex gap-2 sm:gap-3 p-2.5 sm:p-3 bg-white rounded-xl border border-gray-100 hover:shadow-md transition-shadow tap-scale touch-manipulation">
      <img 
        src={product.images?.[0] || '/placeholder.png'}
        alt={product.name}
        className="w-16 h-16 sm:w-20 sm:h-20 object-contain rounded-lg bg-gray-50 shrink-0 cursor-pointer"
        onClick={() => onQuickView(product)}
      />
      <div className="flex-1 min-w-0">
        <p className="text-xs text-primary-500 font-medium mb-0.5 truncate">{product.category}</p>
        <h4 
          className="text-xs font-semibold text-gray-900 line-clamp-2 cursor-pointer hover:text-primary-500 leading-tight overflow-hidden"
          onClick={() => onQuickView(product)}
        >
          {product.name}
        </h4>
        {/* r - nagyobb, szembetn; eredeti thzva pirosan, kedvezmny % */}
        <div className="mt-1.5 text-center min-w-0 space-y-0.5">
          {hasDiscount ? (
            <>
              <span className="text-xs sm:text-sm text-red-600 line-through block font-semibold">{product.price.toLocaleString('hu-HU')} Ft</span>
              <span className="text-base sm:text-lg font-black text-red-600 block">{displayPrice.toLocaleString('hu-HU')} Ft</span>
              <span className="inline-block px-1.5 py-0.5 rounded bg-red-100 text-red-700 text-[10px] sm:text-xs font-bold">-{discountPct}%</span>
            </>
          ) : (
            <span className="text-base sm:text-lg font-black text-gray-900 block">{displayPrice.toLocaleString('hu-HU')} Ft</span>
          )}
        </div>
        {/* Like/Dislike + CTA - Kosrba hangslyosabb */}
        <div className="flex justify-center items-center gap-1 sm:gap-2 mt-2 flex-wrap">
          <button
            type="button"
            onClick={(e) => { e.stopPropagation(); setFeedbackState(toggleLikeProduct(product)); }}
            className={`min-w-[36px] min-h-[36px] p-1.5 flex items-center justify-center rounded-full transition-colors ${feedbackState.liked ? 'bg-green-500 text-white' : 'bg-green-50 text-green-600 hover:bg-green-100'}`}
            aria-label="Tetszik"
          >
            <ThumbsUp className="w-4 h-4" />
          </button>
          <button
            type="button"
            onClick={(e) => { e.stopPropagation(); setFeedbackState(toggleDislikeProduct(product)); }}
            className={`min-w-[36px] min-h-[36px] p-1.5 flex items-center justify-center rounded-full transition-colors ${feedbackState.disliked ? 'bg-red-500 text-white' : 'bg-red-50 text-red-500 hover:bg-red-100'}`}
            aria-label="Nem tetszik"
          >
            <ThumbsDown className="w-4 h-4" />
          </button>
          {(product.inStock ?? product.in_stock) !== false && onAddToCart && (
            <button
              onClick={(e) => { e.stopPropagation(); onAddToCart(product, 1); }}
              className="min-w-[36px] min-h-[36px] p-1.5 flex items-center justify-center rounded-lg bg-emerald-500 text-white hover:bg-emerald-600 transition-colors touch-manipulation shadow-md shadow-emerald-500/30 font-bold"
              title="Kosrba"
            >
              <ShoppingCart className="w-4 h-4" />
            </button>
          )}
          <button
            onClick={() => onCompare?.(product)}
            className={`min-w-[36px] min-h-[36px] p-1.5 flex items-center justify-center rounded-lg transition-colors ${isComparing ? 'bg-primary-100 text-primary-500' : 'hover:bg-gray-100 text-gray-400'}`}
          >
            <GitCompare className="w-4 h-4" />
          </button>
          <button
            onClick={() => onToggleWishlist(product.id)}
            className={`min-w-[36px] min-h-[36px] p-1.5 flex items-center justify-center rounded-lg transition-colors ${isWishlisted ? 'bg-red-100 text-red-500' : 'hover:bg-gray-100 text-gray-400'}`}
          >
            <Star className={`w-4 h-4 ${isWishlisted ? 'fill-current' : ''}`} />
          </button>
        </div>
      </div>
    </div>
  );
};

