const AIBanner = ({ category, onAskAI }) => (
  <div className="bg-gradient-to-r from-primary-500 to-secondary-700 rounded-2xl p-4 sm:p-6 text-white mb-6 animate-fade-in">
    <div className="flex flex-col sm:flex-row items-start sm:items-center gap-4">
      <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center shrink-0">
        <Sparkles className="w-6 h-6" />
      </div>
      <div className="flex-1">
        <h3 className="font-bold text-lg mb-1">AI segt vlasztani</h3>
        <p className="text-white/80 text-sm">Melyik {category.toLowerCase()} illik hozzd?</p>
      </div>
      <button
        onClick={onAskAI}
        className="w-full sm:w-auto px-5 py-2.5 bg-white text-primary-500 rounded-xl font-semibold hover:bg-primary-50 transition-colors tap-scale"
      >
        AI Tancsad
      </button>
    </div>
  </div>
);

/**
 * View & Sort Controls
 */
const ViewControls = ({ 
  sortOption, onSortChange, 
  viewMode, onViewModeChange, 
  loadedCount,
  totalCount,
  onOpenFilters,
  compareCount
}) => (
  <div className="flex flex-wrap items-center justify-between gap-3 mb-4 pb-4 border-b border-gray-100">
    <div className="flex items-center gap-3">
      <button
        onClick={onOpenFilters}
        className="lg:hidden flex items-center gap-2 px-3 py-2 bg-gray-100 rounded-lg text-sm font-medium text-gray-700 tap-scale"
      >
        <Filter className="w-4 h-4" />
        Szrk
      </button>
      <div className="text-sm text-gray-500">
        <p>
          <span className="font-semibold text-gray-900">{loadedCount.toLocaleString('hu-HU')}</span>
          {totalCount > 0 && (
            <span className="text-gray-400 ml-1">/ {totalCount.toLocaleString('hu-HU')}</span>
          )}
          <span className="ml-1">termk</span>
        </p>
        {totalCount > 0 && (
          <div className="mt-1 h-1.5 w-32 bg-gray-100 rounded-full overflow-hidden">
            <div
              className="h-full bg-primary-500 rounded-full transition-all"
              style={{ width: `${Math.min(100, Math.round((loadedCount / totalCount) * 100))}%` }}
            />
          </div>
        )}
      </div>
      {compareCount > 0 && (
        <span className="px-2 py-1 bg-primary-100 text-primary-600 text-xs font-medium rounded-full">
          {compareCount} sszehasonltsra
        </span>
      )}
    </div>
    
    <div className="flex items-center gap-2">
      <select
        value={sortOption}
        onChange={(e) => onSortChange(e.target.value)}
        className="px-3 py-2 text-sm border border-gray-200 rounded-lg bg-white focus:border-primary-500 outline-none"
      >
        <option value="default">Relevancia</option>
        <option value="smart">Okos (ajnlott)</option>
        <option value="price-asc">r </option>
        <option value="price-desc">r </option>
        <option value="name-asc">Nv A-Z</option>
        <option value="discount">Kedvezmny</option>
      </select>
      
      <div className="hidden sm:flex border border-gray-200 rounded-lg overflow-hidden">
        {[
          { mode: 'grid', icon: LayoutGrid },
          { mode: 'compact', icon: Rows3 },
        ].map(({ mode, icon: Icon }) => (
          <button
            key={mode}
            onClick={() => onViewModeChange(mode)}
            className={`p-2 ${viewMode === mode ? 'bg-primary-500 text-white' : 'bg-white text-gray-600 hover:bg-gray-50'}`}
          >
            <Icon className="w-5 h-5" />
          </button>
        ))}
      </div>
    </div>
  </div>
);

/**
 * Main Category Page Component
 */
const CategoryPage = ({
  category,
  products,
  allCategories = [],
  onBack,
  onProductClick,
  onWishlistToggle,
  onCategoryChange,
  wishlist,
  onAddToCart,
  onAskAI,
  totalCount = 0,
  loadedCount = 0,
  stats,
  onLoadMore
}) => {
  const [sortOption, setSortOption] = useState('default');
  const [viewMode, setViewMode] = useState('grid');
  const [filters, setFilters] = useState({});
  const [priceRange, setPriceRange] = useState([0, 10000000]);
  const [showFilters, setShowFilters] = useState(false);
  const [compareList, setCompareList] = useState([]);
  const [presetName, setPresetName] = useState('');
  const [savedPresets, setSavedPresets] = useState([]);
  const loadMoreRef = useRef(null);
  
  const theme = getCategoryTheme(category);
  const likedIds = useMemo(() => getLikedProducts(), []);
  const viewedIds = useMemo(() => getViewedProducts(30).map((p) => p.id), []);

  useEffect(() => {
    try {
      const raw = localStorage.getItem(PRESET_STORAGE_KEY);
      setSavedPresets(raw ? JSON.parse(raw) : []);
    } catch {
      setSavedPresets([]);
    }
  }, [category]);

  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: 'auto' });
  }, [category]);

  const savePreset = () => {
    const name = presetName.trim();
    if (!name) return;
    const next = [
      {
        id: `${Date.now()}`,
        name,
        filters,
        priceRange,
        sortOption,
        viewMode
      },
      ...savedPresets.filter((p) => p.name !== name)
    ].slice(0, 6);
    setSavedPresets(next);
    setPresetName('');
    try {
      localStorage.setItem(PRESET_STORAGE_KEY, JSON.stringify(next));
    } catch {}
  };

  const applyPreset = (preset) => {
    setFilters(preset.filters || {});
    setPriceRange(preset.priceRange || [0, 10000000]);
    setSortOption(preset.sortOption || 'default');
    setViewMode(preset.viewMode || 'grid');
  };

  const quickApply = (type) => {
    if (type === 'in-stock') {
      setFilters((prev) => ({ ...prev, inStockOnly: true }));
      return;
    }
    if (type === 'under-100') {
      setPriceRange([0, 100000]);
      return;
    }
    if (type === 'premium') {
      setPriceRange([250000, priceRange[1]]);
      return;
    }
    if (type === 'discount') {
      setSortOption('discount');
      return;
    }
  };

  const quickCounts = useMemo(() => {
    const inStock = products.filter((p) => (p.inStock ?? p.in_stock ?? true)).length;
    const under100 = products.filter((p) => (p.salePrice || p.price) <= 100000).length;
    const premium = products.filter((p) => (p.salePrice || p.price) >= 250000).length;
    const discount = products.filter((p) => p.salePrice && p.price > p.salePrice).length;
    return { inStock, under100, premium, discount };
  }, [products]);
  
  // Initialize price range from products
  useEffect(() => {
    if (products.length > 0) {
      const prices = products.map(p => p.salePrice || p.price).filter(p => p > 0);
      if (prices.length > 0) {
        setPriceRange([Math.min(...prices), Math.max(...prices)]);
      }
    }
  }, [products]);
  
  // Filter and sort products
  const displayedProducts = useMemo(() => {
    let filtered = [...products];
    
    // Price filter
    filtered = filtered.filter(p => {
      const price = p.salePrice || p.price;
      return price >= priceRange[0] && price <= priceRange[1];
    });
    
    // Brand filter
    if (filters.brands?.length > 0) {
      filtered = filtered.filter(p => filters.brands.includes(p.brand));
    }
    
    // Color filter
    if (filters.colors?.length > 0) {
      filtered = filtered.filter(p => filters.colors.includes(p.color));
    }
    
    // In stock only
    if (filters.inStockOnly) {
      filtered = filtered.filter(p => p.inStock !== false);
    }
    
    // Sort
    switch (sortOption) {
      case 'price-asc':
        filtered.sort((a, b) => (a.salePrice || a.price) - (b.salePrice || b.price));
        break;
      case 'price-desc':
        filtered.sort((a, b) => (b.salePrice || b.price) - (a.salePrice || a.price));
        break;
      case 'name-asc':
        filtered.sort((a, b) => a.name.localeCompare(b.name, 'hu'));
        break;
      case 'discount':
        filtered.sort((a, b) => {
          const dA = a.salePrice ? (a.price - a.salePrice) / a.price : 0;
          const dB = b.salePrice ? (b.price - b.salePrice) / b.price : 0;
          return dB - dA;
        });
        break;
      case 'smart':
        filtered.sort((a, b) => {
          const score = (p) => {
            const stock = typeof p.stock_qty === 'number' ? p.stock_qty : 0;
            const inStock = p.inStock ?? p.in_stock ?? true;
            const liked = likedIds.includes(p.id) ? 6 : 0;
            const viewed = viewedIds.includes(p.id) ? 3 : 0;
            const discount = p.salePrice && p.price ? (p.price - p.salePrice) / p.price : 0;
            return (inStock ? 5 : 0) + liked + viewed + Math.min(stock, 20) * 0.2 + discount * 10;
          };
          return score(b) - score(a);
        });
        break;
    }
    
    return filtered;
  }, [products, sortOption, filters, priceRange]);
  
  const visibleProducts = displayedProducts;
  const hasMoreToShow = totalCount > 0 && loadedCount < totalCount;
  
  // Infinite scroll observer
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting && hasMoreToShow) {
          onLoadMore?.();
        }
      },
      { threshold: 0.1, rootMargin: '200px' }
    );
    
    if (loadMoreRef.current instanceof Element) {
      try {
        observer.observe(loadMoreRef.current);
      } catch (err) {
        return;
      }
    }
    
    return () => observer.disconnect();
  }, [hasMoreToShow, onLoadMore]);
  
  const toggleCompare = (product) => {
    setCompareList(prev => 
      prev.find(p => p.id === product.id)
        ? prev.filter(p => p.id !== product.id)
        : prev.length < 4 ? [...prev, product] : prev
    );
  };
  
  return (
    <div id="products-section" className="min-h-screen bg-gray-50">
      <CategoryHero category={category} productCount={totalCount || products.length} theme={theme} onBack={onBack} />
      
      <div className="w-full max-w-[2000px] mx-auto px-4 sm:px-6 lg:px-10 xl:px-16 py-8 sm:py-12 lg:py-16">
        <AIBanner category={category} onAskAI={onAskAI} />
        <PriceStats products={products} stats={stats} />
        <FeaturedProducts products={products} onProductClick={onProductClick} onWishlistToggle={onWishlistToggle} wishlist={wishlist} onAddToCart={onAddToCart} />
        <RelatedCategories currentCategory={category} allCategories={allCategories} onCategoryChange={onCategoryChange} />
        <CategoryStory category={category} />
        
        <div className="flex gap-8">
          <SidebarFilters
            products={products}
            filters={filters}
            onFilterChange={setFilters}
            priceRange={priceRange}
            onPriceRangeChange={setPriceRange}
            isOpen={showFilters}
            onClose={() => setShowFilters(false)}
          />
          
          <div className="flex-1 min-w-0">
            <div className="mb-4 rounded-2xl border border-gray-100 bg-white p-4 shadow-sm">
              <div className="flex flex-wrap items-center gap-2 mb-3">
                <span className="text-xs font-semibold text-gray-500">Gyors szrk:</span>
                <button onClick={() => quickApply('in-stock')} className="px-3 py-1.5 rounded-full text-xs font-semibold border border-gray-200 hover:bg-gray-50">Kszleten ({quickCounts.inStock})</button>
                <button onClick={() => quickApply('under-100')} className="px-3 py-1.5 rounded-full text-xs font-semibold border border-gray-200 hover:bg-gray-50">100k alatt ({quickCounts.under100})</button>
                <button onClick={() => quickApply('premium')} className="px-3 py-1.5 rounded-full text-xs font-semibold border border-gray-200 hover:bg-gray-50">Prmium ({quickCounts.premium})</button>
                <button onClick={() => quickApply('discount')} className="px-3 py-1.5 rounded-full text-xs font-semibold border border-gray-200 hover:bg-gray-50">Akcik ({quickCounts.discount})</button>
              </div>
              <div className="flex flex-wrap items-center gap-2">
                <input
                  type="text"
                  value={presetName}
                  onChange={(e) => setPresetName(e.target.value)}
                  placeholder="Preset neve"
                  className="px-3 py-2 border border-gray-200 rounded-lg text-sm"
                />
                <button onClick={savePreset} className="px-3 py-2 rounded-lg bg-primary-500 text-white text-sm font-semibold">Ments</button>
                {savedPresets.map((preset) => (
                  <button
                    key={preset.id}
                    onClick={() => applyPreset(preset)}
                    className="px-3 py-2 rounded-lg bg-gray-100 text-gray-700 text-sm font-semibold hover:bg-gray-200"
                  >
                    {preset.name}
                  </button>
                ))}
              </div>
            </div>
            <ViewControls
              sortOption={sortOption}
              onSortChange={setSortOption}
              viewMode={viewMode}
              onViewModeChange={setViewMode}
              loadedCount={loadedCount}
              totalCount={totalCount}
              onOpenFilters={() => setShowFilters(true)}
              compareCount={compareList.length}
            />
            
            {viewMode === 'grid' ? (
              <div className="product-grid">
                {visibleProducts.map((product, index) => (
                  <EnhancedProductCard
                    key={product.id}
                    product={product}
                    onQuickView={onProductClick}
                    onToggleWishlist={onWishlistToggle}
                    onAddToCart={onAddToCart}
                    isWishlisted={wishlist?.includes(product.id)}
                    index={index}
                    sectionId={`category-${category}`}
                  />
                ))}
              </div>
            ) : (
              <div className="space-y-3">
                {visibleProducts.map(product => (
                  <CompactProductCard
                    key={product.id}
                    product={product}
                    onQuickView={onProductClick}
                    onToggleWishlist={onWishlistToggle}
                    onAddToCart={onAddToCart}
                    isWishlisted={wishlist?.includes(product.id)}
                    onCompare={toggleCompare}
                    isComparing={compareList.some(p => p.id === product.id)}
                  />
                ))}
              </div>
            )}
            
            {/* Infinite scroll trigger */}
            {hasMoreToShow && (
              <div ref={loadMoreRef} className="flex justify-center py-8">
                <div className="w-8 h-8 border-3 border-primary-500 border-t-transparent rounded-full animate-spin" />
              </div>
            )}
            
            {!hasMoreToShow && visibleProducts.length > 0 && (
              <p className="text-center text-gray-400 text-base lg:text-lg py-10 lg:py-12">
                Minden termk betltve ({displayedProducts.length} db)
              </p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default CategoryPage;
